<template>
  <div>
    <el-form style="width: 400px" :model="createForm" :rules="createFormRules" ref="createForm" label-width="100px">

      <el-form-item label="" prop="name">
        <el-input v-model="createForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="cardId">
        <el-input v-model="createForm.cardId"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-select v-model="createForm.sex" placeholder="">
          <el-option label="" value="1"/>
          <el-option label="" value="2"/>
        </el-select>
<!--        <el-input v-model="createForm.sex"></el-input>-->
      </el-form-item>
      <el-form-item label="" prop="position">
        <el-select v-model="createForm.position" placeholder="">
          <el-option v-for="item in positionList" :key="item.value" :label="item.name" :value="item.id"/>
        </el-select>
<!--        <el-input v-model="createForm.position"></el-input>-->
      </el-form-item>
      <el-form-item label="" prop="academic">
        <el-input v-model="createForm.academic"></el-input>
      </el-form-item>
      <el-form-item label="" prop="mail">
        <el-input v-model="createForm.mail"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="createForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input v-model="createForm.address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="department">
        <el-select v-model="createForm.department" placeholder="">
          <el-option v-for="item in departmentList" :key="item.value" :label="item.name" :value="item.id"/>
        </el-select>
<!--        <el-input v-model="createForm.department"></el-input>-->
      </el-form-item>
      <el-form-item label="" prop="postCode">
        <el-input v-model="createForm.postCode"></el-input>
      </el-form-item>
      <el-form-item label="QQ" prop="qq">
        <el-input v-model="createForm.qq"></el-input>
      </el-form-item>
      <el-form-item label="" prop="party">
        <el-input v-model="createForm.party"></el-input>
      </el-form-item>
      <el-form-item label="" prop="birthday">
        <el-input v-model="createForm.birthday"></el-input>
      </el-form-item>
      <el-form-item label="" prop="race">
        <el-input v-model="createForm.race"></el-input>
      </el-form-item>
      <el-form-item label="" prop="speciality">
        <el-input v-model="createForm.speciality"></el-input>
      </el-form-item>
      <el-form-item label="" prop="hobby">
        <el-input v-model="createForm.hobby"></el-input>
      </el-form-item>
      <el-form-item label="" prop="remark">
        <el-input v-model="createForm.remark"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="CreatUser('createForm')"></el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
  import {CreateStaff} from "../api/hrmStaff";
import axios from 'axios'
  export default {
        name: "HrmStaffEdit",
        data(){
            return{
                positionList:null,
                departmentList:null,
                createForm:{
                    name:'test',
                    sex:'1',
                    phone:'16601169545',
                    mail:'123456789@qq.com',
                    position:'',
                    academic:'',
                    department:'',
                    address:'',
                    postCode:'12345',
                    cardId:'123456789038475612',
                    qq:'483286868',
                    party:'',
                    birthday:'19970101',
                    race:'',
                    speciality:'',
                    hobby:'',
                    remark:''
                },
                createFormRules:{
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    sex: [
                        { required: true, message: '', trigger: 'change' },
                    ],
                    phone: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    mail: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    position: [
                        { required: true, message: '', trigger: 'change' },
                    ],
                    academic: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    department: [
                        { required: true, message: '', trigger: 'change' },
                    ],
                    address: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    postCode: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    cardId: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    qq: [
                        { required: true, message: 'QQ', trigger: 'blur' },
                    ],
                    party: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    birthday: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    race: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    speciality: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    hobby: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    remark: [
                        { required: true, message: '', trigger: 'blur' },
                    ]
                }
            }
        },
        methods:{
            CreatUser(formName) {
                let that=this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        // creat item
                        // let temp={
                        //     id:that.createForm.id,
                        //     name:that.createForm.name,
                        //     sex:that.createForm.sex,
                        //     phone:that.createForm.phone,
                        //     mail:that.createForm.mail,
                        //     position:that.createForm.position,
                        //     academic:that.createForm.academic,
                        //     department:that.createForm.department,
                        //     address:that.createForm.address
                        // }
                        CreateStaff(that.createForm).then(function (e) {
                            if(e.status=='ok'){
                                that.$message({
                                    message:'',
                                    type:'success'
                                })
                            }else {
                                that.$message({
                                    message:e.data,
                                    type:'error'
                                })
                            }
                        }).catch(function (e) {
                            console.log(e)
                        })
                    } else {
                        return false;
                    }
                });
            }
        },
      mounted() {
          let that=this
          axios({
              methods: 'get',
              url:'http://127.0.0.1:2333/api/getlist'
          }).then(function (e) {
              console.log(e.data)
              that.positionList=e.data.data.position
              that.departmentList=e.data.data.department
          }).catch(function (e) {
              console.log(e)
          })
      }
  }
</script>

<style scoped>

</style>



